.arrow-container {
  position: relative;
  max-width: 1810px;
  width: 94.2708vw;
  .margin-inline(@a);
}

.arrow-container .swiper-button-next,
.arrow-container .swiper-button-prev {
  color: #dadada!important;
}

.arrow-container .container {
  max-width: 1625px;
  width: 84.6354vw;

  box-shadow: 0 0 153px 27px rgba(17, 30, 66, 0.05);
}

.swiper-button-prev,
.swiper-container-rtl .swiper-button-next {
  left: 0.52083vw!important;
}

.swiper-button-next,
.swiper-container-rtl .swiper-button-prev {
  right: 0.52083vw!important;
}

.rehab-swiper {
  margin-bottom: @d192px;
}

.rehab-swiper__slide {
  display: flex;
  flex-direction: column;
  width: 100%;

  background-color: #ffffff;
  box-shadow: 0 0 153px 27px rgba(17, 30, 66, 0.05);
  border-radius: 7px;
}

.rehab-swiper__link-pages {
  display: none;

  text-decoration: none;
  color: #14b250;
}

.rehab-swiper__img-wrapper {
  max-height: 372px;
  height: @d372px;
}

.rehab-swiper__img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  border-radius: 7px 7px 0 0;
}

.rehab-swiper__text-wrapper {
  box-sizing: border-box;
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  padding-top: @d37px;
  .padding-inline(@d40px);
  padding-bottom: @d43px;
}

