  //     padding-bottom: @m26px;
  //   }
  // }
}

@media (min-width: @max-desktop-width) {
  .rehab-swiper {
    margin-bottom: 192px;
  }

  .rehab-swiper__text-wrapper {
    padding-top: 37px;
    .padding-inline(40px);
    padding-bottom: 43px;
  }

  .rehab-swiper__city {
    padding-bottom: 4px;
    font-size: 27px;
  }

  .rehab-swiper__street {
    padding-bottom: 26px;
    font-size: 30px;
    font-weight: 700;
  }

  .rehab-swiper__place-description {
    width: 338px;
    padding-bottom: 17px;
    font-size: 18px;
    line-height: 24px;
  }

  .rehab-swiper__price {
    font-size: 23px;
    line-height: 50px;
  }

  .rehab-swiper__link-page {
    bottom: 50px;
    right: 72px;
  }

  // .rehab-swiper--clinics {
