  //     padding-bottom: 26px;
  //   }
  // }

  .detox-page {
    .rehab-swiper {
      margin-bottom: 206px;
    }
    .rehab-swiper__img-wrapper {
      height: 278px;
    }
    .rehab-swiper__text-wrapper {
      padding-top: 29px;
      .padding-inline(33px);
      padding-bottom: 23px;
    }
    .rehab-swiper__city {
      font-size: 20px;
    }
    .rehab-swiper__street {
      margin-bottom: 2px;
      font-size: 22px;
    }
    .rehab-swiper__place-description {
      margin-bottom: 9px;
      font-size: 14px;
      line-height: normal;
    }
    .rehab-swiper__price {
      font-size: 17px;
      line-height: normal;
    }
    .rehab-swiper__link-page {
      bottom: 24px;
      right: 45px;
      width: 25px;
      height: 25px;
    }
  }
}

